import React from "react"
import { Container, Row, Col } from "react-bootstrap"
import postData from "../information/posts"
import PostItem from "../components/PostItem"
import TopNavBar from "../navigation/NavBar"
import Footer from "../navigation/Footer"
import Layout from "../components/Layout"
import SEO from "../components/seo"

const description =
  "A list of my blog posts in Hybrid Mobile Technology, primarily React Native and Flutter."

class Posts extends React.Component {
  render() {
    return (
      <Layout>
        <SEO title="Blog Posts" description={description} />
        <TopNavBar />
        <header className="masthead sm">
          <Container className="h-100">
            <Row className="h-100 align-items-center">
              <Col className="text-center">
                <h2>Blog Posts</h2>
                <h5>{description}</h5>
              </Col>
            </Row>
          </Container>
        </header>
        <Container fluid>
          <Row>
            <Col lg={{ span: 8, offset: 2 }}>
              <section className="py-5" id="about">
                {postData.map((item, index) => (
                  <PostItem key={index} item={item} />
                ))}
              </section>
            </Col>
          </Row>
        </Container>
        <Footer />
      </Layout>
    )
  }
}

export default Posts
